import { Component, OnInit, ViewChild } from '@angular/core';
import { NvD3Module } from 'angular2-nvd3';
import * as d3        from 'd3';
// COMPONENTS
import { PieComponent }      from '../../../components/basic/charts/pie/pie.component';
import { SunburstComponent } from '../../../components/basic/charts/sunburst/sunburst.component';

// SERVICES
import { ReportsService } from '../../../providers/reports/reports.service';

@Component({
  selector: 'app-data-general',
  templateUrl: './data-general.component.html',
  styleUrls: ['./data-general.component.scss']
})
export class DataGeneralComponent implements OnInit {
	@ViewChild(PieComponent)      private pieComponent: PieComponent;
	@ViewChild(SunburstComponent) private sunburstComponent: SunburstComponent;

	navs:Array<any> = [
    {id:0, name:'Generales',            subNav:'general',   viewState: false, state: null},
    {id:1, name:'Descargas',            subNav:'downloads', viewState: false, state: null},
    {id:2, name:'Usuarios Registrados', subNav:'users',     viewState: false, state: null}
  ];

	dataChartPie1:any;
	paramsChartPie1:any = {
		id:'ChartPie1',
		title:'Descargas',
		mostrarNombre:true,
		size:300,
		topLegends: 210
	};

	dataChartPie2:any;
	paramsChartPie2:any = {
		id:'ChartPie2',
		title:'Registro',
		mostrarNombre:true,
		size:300,
		topLegends: 210
	};

	dataSunburst:any;
	paramsSunburst:any = {
		id:'ChartSunburst',
		title:'Registro Todos',
		size: 500
	};

	cardSessions:any = {};
	cards:Array<any> = [];

	constructor(
		private reportsService:ReportsService) {}


  ngOnInit() {

		// CHARTS
		this.getChartAllRecords();
		this.getChartDownload();
		this.getChartLoginTypes();

		// CARDS
		this.getCardSessions();
		this.getCardDownloads();
		this.getCardUsers();

	}

	// SUNBURST CHART
	getChartAllRecords(){
		this.reportsService.getChartAllRecords().then(
			success => {
				this.dataSunburst = success;
			}
		);
	}

	// PIE CHART
	getChartDownload(){
		this.reportsService.getChartDownloads().then(
			success => {
				this.dataChartPie1 = success.data;
			}
		);
	}
	getChartLoginTypes(){
		this.reportsService.getChartLoginTypes().then(
			success => {
				this.dataChartPie2 = success.data;
			}
		);
	}

	// CARDS
	getCardSessions(){
		this.reportsService.getCardSessions().then(
			success => {
				this.cardSessions = success;
			}
		);
	}
	getCardDownloads(){
		this.reportsService.getCardDownloads().then(
			success => {
				this.cards.push(success);
			}
		);
	}
	getCardUsers(){
		this.reportsService.getCardUsers().then(
			success => {
				this.cards.push(success);
			}
		);
	}


}



