import { Component, OnInit, Input } from '@angular/core';
import { NvD3Module } 			 from 'angular2-nvd3';
import * as d3               from 'd3';


@Component({
  selector: 'chart-pie',
  templateUrl: './pie.component.html',
  styleUrls: ['./pie.component.scss']
})
export class PieComponent implements OnInit {
	@Input('params') params:any;
	@Input('data') data:Array<any>;
	options:any;


	constructor() {}

	ngOnInit() {
		
		this.options = {
			chart: {
				type: 'pieChart',
				title: this.params.title,
				height: 500,
				width: this.params.size,
				padAngle: 0.08,
				cornerRadius: 5,
				donut: true,
				margin:{left:0,right:0,top:-20,bottom:0},
				x: (d) => {return d.name;},
				y: (d) => {return d.total;},
				showLegend: true,
				color:(d) => {return d.data.colorText;},
				pie: {
					labelsOutside: true,
					donut: true,
					showLabels: this.params.mostrarNombre
				},
				duration: 500,
				legend: {
					padding:50,
					margin: {
						top: this.params.topLegends,
						right:0,
						bottom: 0,
						left: 0
					}
				},

				callback: (chart) => {
					// d3.select(".nv-legendWrap").attr("transform","translate(0,450)");
					d3.selectAll('.nv-pieLabels text').style('fill', "#999");
					d3.selectAll('.nv-legend text').style('fill', "#999");
					d3.selectAll('.nv-pie-title').style('fill', "#ccc");

					chart.pie.dispatch.on("elementClick", (e) => {
						let xIcon = (this.params.title == 'Calificaciones') ? '48%' : '46%';
						d3.select("app-nvd3#" + this.params.id).selectAll(".nv-pie-title").text(e.data.percent).style("font-size", "40px");

						d3.select("app-nvd3#" + this.params.id).select(".nv-pieWrap.nvd3-svg").select("image").remove();
						d3.select("app-nvd3#" + this.params.id).select(".nv-pieWrap.nvd3-svg").select("rect").remove();

						if(this.params.title == 'Calificaciones'){
							d3.select("app-nvd3#" + this.params.id).select(".nv-pieWrap.nvd3-svg").selectAll('text#textRating').remove();
							d3.select("app-nvd3#" + this.params.id).select(".nv-pieWrap.nvd3-svg").append("text").text(e.data.data.name)
							.attr('id','textRating')
							.attr('x','44%')
							.attr('y','61.5%')
							.style("font-size", "20px")
							.style('fill', "#ccc");
						}else if(this.params.title == 'Itinerarios' || this.params.title == 'Favoritos' || this.params.title == 'Comentarios' || this.params.title == 'Contenidos'){
							d3.select("app-nvd3#" + this.params.id).selectAll(".nv-pieWrap")
							.append("svg")
							.attr('x',xIcon)
							.attr('y','57%')
							.attr('width', 30)
							.attr('height', 30)
							.append("rect")
							.attr('width', 30)
							.attr('height', 30)
							.attr('rx', 3)
							.attr('ry', 3)
							.style("fill", "#bbb");
						}

						d3.select("app-nvd3#" + this.params.id).selectAll(".nv-pieWrap")
						.append("svg:image")
						.attr('x',xIcon)
						.attr('y','57%')
						.attr('width', 30)
						.attr('height', 30)
						.attr("xlink:href",e.data.data.image);

					});

					chart.legend.dispatch.on("legendClick", (e) => {
						d3.select("app-nvd3#" + this.params.id).select(".nv-pieWrap.nvd3-svg").select("rect").remove();
						d3.select("app-nvd3#" + this.params.id).select(".nv-pieWrap.nvd3-svg").select("image").remove();
						if(this.params.title == 'Calificaciones'){
							d3.select("app-nvd3#" + this.params.id).select(".nv-pieWrap.nvd3-svg").selectAll('text#textRating').remove();
						}
						console.log('Click legend in callback', JSON.stringify(e));
					});


				}

			}
	  };
	}

}



