import { Component, OnInit, ViewChild } from '@angular/core';
//COMPONENTS
import { ImageUploadComponent }         from '../../../../components/basic/image-upload/image-upload.component';
import { ImageUploadCircleComponent }   from '../../../../components/basic/image-upload-circle/image-upload-circle.component';
import { ChipsComponent }               from '../../../../components/basic/chips/chips.component';
//PROVIDERS
import { DataSectionsService }          from '../../../../providers/sections/data-sections.service';

@Component({
  selector: 'app-informative-profile',
  templateUrl: './informative-profile.component.html',
  styleUrls: ['./informative-profile.component.scss']
})
export class InformativeProfileComponent implements OnInit {
	@ViewChild(ImageUploadComponent)       imageUpload:ImageUploadComponent;
	@ViewChild(ImageUploadCircleComponent) imageUploadCircle:ImageUploadCircleComponent;
	@ViewChild(ChipsComponent)             chipsComponent:ChipsComponent;

	objectSection:any = {};
	dataChips:Array<any>;
	enableBoxImages:boolean;

	constructor(
		private dataSectionsService:DataSectionsService) {}

	ngOnInit() {
		// OBJETO CATEGORIA
		this.objectSection = this.dataSectionsService.getTemporarySection();
		// CONFIGURACION DE COMPONENTS
		this.chipsComponent.enableDeleteChip = false;
		this.imageUploadCircle.changeStyleConatiner(true);
		this.enableBoxImages = false;

		this.asignacionDeSecciones();
	}

	ngAfterViewInit(){}

	asignacionDeSecciones(){
		let isEditionOfNew = this.dataSectionsService.getIsEditionOfNew();
		let isEdition = this.dataSectionsService.getIsEdition();
		let isNewEdition = !isEditionOfNew && !isEdition;
		let isThereReording = this.dataSectionsService.isThereReordingSections();

		if(isNewEdition && !isThereReording){
			this.dataChips = this.dataSectionsService.addNewSectionToCatalog();
		}
		if(isNewEdition && isThereReording){
			this.dataChips = this.dataSectionsService.getReordingSections();
		}
		if(isEdition && isEditionOfNew && isThereReording){
			this.dataChips = this.dataSectionsService.getReordingSections();
		}
		if(isEdition && isEditionOfNew && !isThereReording){
			this.dataChips = this.dataSectionsService.addNewSectionToCatalog();
		}
		if(isEdition && !isEditionOfNew && !isThereReording){
			this.dataChips = this.dataSectionsService.getCatalogSections();
		}
		if(isEdition && !isEditionOfNew && isThereReording){
			this.dataChips = this.dataSectionsService.getReordingSections();
		}
	}

	reorderChips(items:any){
		items.forEach(item => {
			item.prioridad = items.indexOf(item);
			if(item.esNew){
				this.objectSection.prioridad = items.indexOf(item);
			}
		});

		this.dataSectionsService.setReordingSection(items);

	}

	setIcon(icon:any){
		this.objectSection.estilo.icono = icon;
	}

	setImage(image:any){
		this.objectSection.imagen = image;
	}

	deleteImage(image:any){
		this.objectSection.imagen = '';
	}



	// GUARDS
	canDeactivate(): Promise<boolean> | boolean {
		this.dataSectionsService.setStateData(0,this.validateObjectProfile());
		return true;
	}

	validateObjectProfile():boolean{
		let isValid:boolean = true;

		if(	 !this.objectSection.nombre
			|| !this.objectSection.descripcion
			|| !this.objectSection.imagen
			|| (!this.objectSection.estilo.clase  ||  this.objectSection.estilo.clase  === '#fff')
			|| (!this.objectSection.estilo.clase2 ||  this.objectSection.estilo.clase2 === '#fff')
			|| !this.objectSection.estilo.icono){
				isValid = false;
			}

			return isValid;
	}


	}



