import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AddServiceComponent } from './add-service/add-service.component';

import { ProfileServiceComponent }          from './add-service/profile-service/profile-service.component';
import { SectionFeaturesServiceComponent }  from './add-service/section-features-service/section-features-service.component';


const routes: Routes = [
	{
      path: 'admin-services',
	  children: [
		  {
			  path: 'add',
			  component: AddServiceComponent,
			  children: [
				  {
					  path: 'profile-service',
					  component: ProfileServiceComponent,
					  outlet: 'sub',
				  },
				  {
					  path: 'section-feature',
					  component: SectionFeaturesServiceComponent,
					  outlet: 'sub',
				  }
			  ]
		  }
	  ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class AdminServicesRoutingModule { }



