import { Injectable }    from '@angular/core';
import * as _            from 'lodash';
// DATA MOCK
import * as mock         from '../../models/data/dataMocks';

import { HttpClient, HttpHeaders }    from '@angular/common/http'
import { Observable } from 'rxjs';



@Injectable()
export class ReportsService {

  constructor(private http: HttpClient) { }

	// SUNBURST CHART
	getChartActionsUser(): Promise<any[]> {
	  return Promise.resolve(mock.CHART_SUNBURST_MOCK);
	}
	getChartAllRecords(): Promise<any[]> {
	  return Promise.resolve(mock.CHART_SUNBURST_MOCK);
  }
	getChartViews(): Promise<any[]> {
	  return Promise.resolve(mock.CHART_SUNBURST_MOCK);
	}

	// PIE CHART
	getChartDownloads(): Promise<any> {
	  return Promise.resolve(mock.CHART_PIE_DOWNLOADS);
	}
	getChartLoginTypes(): Promise<any> {
	  return Promise.resolve(mock.CHART_PIE_LOGIN);
	}
	getChartCalifications(): Promise<any> {
	  return Promise.resolve(mock.CHART_PIE_CALIFICATIONS);
  }
	getChartFavorites(): Promise<any> {
	  return Promise.resolve(mock.CHART_PIE_MOCK);
	}
	getChartItineraries(): Promise<any> {
	  return Promise.resolve(mock.CHART_PIE_MOCK);
	}
	getChartComments(): Promise<any> {
	  return Promise.resolve(mock.CHART_PIE_MOCK);
	}
	getChartContents(): Observable<any> {

		return this.http.post<any>('http://192.168.1.70:8080/TurismoApp/pieChart/contents1',  
			{data:{}}, 
			{headers: new HttpHeaders().set('Content-Type', 'application/json')}
			);
		
	  // return Promise.resolve(mock.CHART_PIE_MOCK);
	}

	
	
	// CARDS
	getCardCalifications(): Promise<any> {
	  return Promise.resolve(mock.CARD_CALIFICATIONS);
	}
	getCardFavorites(): Promise<any> {
		return Promise.resolve(mock.CARD_FAVORITES);
	}
	getCardItineraries(): Promise<any> {
		return Promise.resolve(mock.CARD_ITINERARIES);
	}
	getCardComments(): Promise<any> {
		return Promise.resolve(mock.CARD_COMMENTS)
	}

	getCardSessions(): Promise<any> {
		return Promise.resolve(mock.CARD_SESSIONS);
	}
	getCardDownloads(): Promise<any> {
		return Promise.resolve(mock.CARD_DOWNLOADS);
	}
	getCardUsers(): Promise<any> {
		return Promise.resolve(mock.CARD_USERS);
	}

	getCardViews(): Promise<any> {
		return Promise.resolve(mock.CARD_VIEWS)
	}
	getCardContents(): Promise<any> {
		return Promise.resolve(mock.CARD_CONTENTS)
	}




}



