import { Component, OnInit, EventEmitter, Output, Input } from '@angular/core';

@Component({
  selector: 'app-table-section-features',
  templateUrl: './table-section-features.component.html',
  styleUrls: ['./table-section-features.component.scss']
})
export class TableSectionFeaturesComponent implements OnInit {
	rows: any= [];
	@Output() outSaveFeature   = new EventEmitter<any>();
    @Output() outCancelFeature = new EventEmitter<any>();
	columns: any = [];
	constructor() {
		this.columns = [
	      { name: '#', prop: '$$index'},
	      { name: 'Seccin'}
	    ];
		// this.rows = {id: 200, seccion: ''};
	}

	ngOnInit() {
		// this.rows = {id: 200, seccion: 'prueba'};
	}

	eliminar(row) {
      this.rows.splice(this.rows.indexOf(row), 1);
    }

	desableRows() {
      this.rows.forEach(item => {
        item.enableRow = true;
      });
    }

	setRows(rows:any){
		this.rows = rows;
	}
}



