import { Component, OnInit, EventEmitter, Output, Input, ViewChild } from '@angular/core';
import * as _         from 'lodash';

// COMPONENTS
// import { AlertComponent }   from '../dialogs/alert/alert.component';

@Component({
  selector: 'component-table-add-category-informative',
  templateUrl: './table-add-category-informative.component.html',
  styleUrls: ['./table-add-category-informative.component.scss']
})
export class TableAddCategoryInformativeComponent implements OnInit {
	// @ViewChild(AlertComponent) private dialogAlert: AlertComponent;
  @Input('data') row: any;
  @Input('flag') flag: string;
  @Input('catalogOptions') selectOptions: Array<any>;
  @Output() outSaveFeature   = new EventEmitter<any>();
  @Output() outCancelFeature = new EventEmitter<any>();

	enableBoxImages:boolean = true;
	rowCopy:any;
  // columns = [
  //   { name: 'Nombre'},
  //   { name: 'Tipo'},
  //   { name: 'Para vista'},
  //   { name: 'Para filtrar'},
  //   { name: 'cono'}
  // ];


  constructor() {}

  ngOnInit() {
		this.rowCopy = _.cloneDeep(this.row);
		this.formatedData();
	}

	////////
	 formatedData(){
		 for(let i = 0; i < 6; i++){
			 let newImage = {idImagen: i, nombre: '', tipoContenido: '', url:'', prioridad: null, esMiniatura: false};

			 if(!this.row.imagenes[i]){
				 this.row.imagenes.push(newImage);
			 }else{
				 this.row.imagenes[i].idImagen = i;
			 }
		 }

		 console.log("==>> EN formatedData " + JSON.stringify(this.row.imagenes));
	 }
 	setImage(image:any){

 		_.remove(this.row.imagenes, (item) => {return (!item.url);});

 	// 	if(this.row.imagenes.length < 6){
 			this.row.imagenes.push(image);
 			this.formatedData();
 	// 	}else{
			// this.formatedData();
			// this.dialogAlert.openModal('Slo puedes guardar 6 imagenes');
		// }

 	}


 	deleteImage(image:any){
 		this.row.imagenes.splice(this.row.imagenes.indexOf(image), 1);
 		this.formatedData();
 	}
	////////

  saveNewFeature(){
    this.outSaveFeature.emit({data:this.row, flag:this.flag});
  }

  cancelNewFeature(){
    this.outCancelFeature.emit();
  }

  // setIcon(icon:any){
  //   this.row.categoria.metadato.imagen = icon;
  // }

	changeData():boolean{
		return (_.isEqual(this.row, this.rowCopy));
	}

	// outAlertDialog(eve:any){
		// console.log("==>> EN outAlertDialog..." + eve);
		// this.formatedData();
	// }

}



