import { NgModule, CUSTOM_ELEMENTS_SCHEMA } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { CommonModule }                     from '@angular/common';
// Componentes de terceros
import { MaterializeModule}     from 'angular2-materialize';
import { NgxDatatableModule }   from '@swimlane/ngx-datatable';
import { DndModule}             from 'ng2-dnd';
import { ColorPickerModule }    from 'angular2-color-picker';
import { NvD3Module } 				  from 'angular2-nvd3';

// Service
import { BaseService }          from '../../providers/commons/baseService.service';

// Basic components
import { CardComponent }              from '../../components/basic/card/card.component';
import { TableComponent }             from '../../components/basic/table/table.component';
import { TableAddFeatureComponent }   from '../../components/basic/table-add-feature/table-add-feature.component';
import { ColumnsComponent }           from '../../components/basic/columns/columns.component';
import { ChipsComponent }             from '../../components/basic/chips/chips.component';
import { SearchBarComponent }         from '../../components/basic/search-bar/search-bar.component';
import { ConfirmComponent }           from '../../components/basic/dialogs/confirm/confirm.component';
import { ImageUploadComponent }       from '../../components/basic/image-upload/image-upload.component';
import { ImageUploadCircleComponent } from '../../components/basic/image-upload-circle/image-upload-circle.component';
import { TableAddCategoryComponent }  from '../../components/basic/table-add-category/table-add-category.component';
import { AlertComponent }             from './../../components/basic/dialogs/alert/alert.component';
import { DynamicFormsComponent }      from '../../components/basic/dynamic-forms/dynamic-forms.component';
import { DynamicFormElementComponent }from '../../components/basic/dynamic-forms/dynamic-form-element.component';
import { TableSectionFeaturesComponent }        from './../../components/basic/table-section-features/table-section-features.component';
import { IsInformativePipe } 					          from './../../pipes/sections/is-informative.pipe';
import { TableAddCategoryInformativeComponent } from './../../components/basic/table-add-category-informative/table-add-category-informative.component';
import { TableCategoriesInformativeComponent }  from './../../components/basic/table-categories-informative/table-categories-informative.component';
import { PieComponent }      from './../../components/basic/charts/pie/pie.component';
import { SunburstComponent } from './../../components/basic/charts/sunburst/sunburst.component';
import { TableSectionsServiceComponent }        from '../../components/basic/table-sections-service/table-sections-service.component';
import { AddSectionServiceComponent } from '../../components/basic/add-section-service/add-section-service.component';

@NgModule({
  imports: [
    CommonModule,
	  FormsModule,
	  ReactiveFormsModule,
    // componentes terceros
    MaterializeModule,
    NgxDatatableModule,
    DndModule,
    ColorPickerModule,
		NvD3Module
  ],
  exports: [
    CardComponent,
    TableComponent,
    TableAddFeatureComponent,
    TableAddCategoryComponent,
		TableSectionFeaturesComponent,
		TableAddCategoryInformativeComponent,
		TableCategoriesInformativeComponent,
		TableSectionsServiceComponent,
    ColumnsComponent,
    ChipsComponent,
    SearchBarComponent,
    ConfirmComponent,
    AlertComponent,
    ImageUploadComponent,
    ImageUploadCircleComponent,
    IsInformativePipe,
    AddSectionServiceComponent,
    // SeccionCategoriasComponent,
		DynamicFormsComponent,
		DynamicFormElementComponent,
    // componentes terceros
    MaterializeModule,
    NgxDatatableModule,
    DndModule,
    ColorPickerModule,
    PieComponent,
    SunburstComponent
  ],
  declarations: [
    CardComponent,
    TableComponent,
    TableAddFeatureComponent,
    TableAddCategoryComponent,
		TableSectionFeaturesComponent,
		TableAddCategoryInformativeComponent,
		TableCategoriesInformativeComponent,
		TableSectionsServiceComponent,
    ColumnsComponent,
    ChipsComponent,
    SearchBarComponent,
    ConfirmComponent,
    AlertComponent,
		IsInformativePipe,
    ImageUploadComponent,
    ImageUploadCircleComponent,
    DynamicFormsComponent,
		DynamicFormElementComponent,
		PieComponent,
		SunburstComponent,
		AddSectionServiceComponent
  ],
	providers: [BaseService],
  schemas: [ CUSTOM_ELEMENTS_SCHEMA ]
})
export class BasicComponentsModule { }



