import { NgModule }     from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule }  from '@angular/forms';

import { NvD3Module } 				  from 'angular2-nvd3';

import { ReportsRoutingModule } from './reports-routing.module';
import { DataGeneralComponent } from './data-general/data-general.component';
import { DataUserComponent }    from './data-user/data-user.component';
import { DataAppComponent }     from './data-app/data-app.component';
import { GeneralComponent }     from './data-general/general/general.component';
import { DownloadsComponent }   from './data-general/downloads/downloads.component';
import { UsersComponent }       from './data-general/users/users.component';
import { SectionsComponent }    from './data-app/sections/sections.component';
import { CategoriesComponent }  from './data-app/categories/categories.component';
import { FeaturesComponent }    from './data-app/features/features.component';
import { ActionsComponent }     from './data-user/actions/actions.component';
import { RatingsComponent }     from './data-user/ratings/ratings.component';
import { FavoritesComponent }   from './data-user/favorites/favorites.component';
import { ItinerariesComponent } from './data-user/itineraries/itineraries.component';
import { CommentsComponent }    from './data-user/comments/comments.component';

// MODULES
import { BasicComponentsModule } from '../basic-components/basic-components.module';
// SERVICES
import { ReportsService }        from './../../providers/reports/reports.service';

@NgModule({
  imports: [
    CommonModule,
		FormsModule,
    ReportsRoutingModule,
    //modules
    BasicComponentsModule,
		//graficas
		NvD3Module
  ],
  declarations: [
		DataGeneralComponent,
		DataUserComponent,
		DataAppComponent,
		GeneralComponent,
		DownloadsComponent,
		UsersComponent,
		SectionsComponent,
		CategoriesComponent,
		FeaturesComponent,
		ActionsComponent,
		RatingsComponent,
		FavoritesComponent,
		ItinerariesComponent,
		CommentsComponent],
  providers: [ReportsService]
})
export class ReportsModule { }



