import { Injectable }                         from '@angular/core';
import { Response, Http, Headers}             from '@angular/http';
import { environment }                        from '../../../environments/environment';
import * as modelos                           from '../../models/Models';
import 'rxjs/add/operator/timeout';


@Injectable()
export class BaseService {

	private url: string;

	constructor(private http: Http) {
		this.url = environment.endpoints.core;
	}



	public post<U>(uri: string, data: any, propertySelector: (response: modelos.Response<U>) => U): Promise<U> {
		const url = this.url + uri;
		const options = { headers: new Headers() };
		const promise: Promise<U> = new Promise((resolve, reject) => {
			this.http.post(url, data, options)
				.timeout(20000)
				.subscribe((response: Response) => {
					const res = response.json() as modelos.Response<U>;
					let resposeData: U = null;
					console.log('[Request Ok]:' + JSON.stringify(res));
					if (res.isSuccess) {
						resposeData = propertySelector(res);
						console.log('Response [Success]: ' + JSON.stringify(resposeData));
					}
					if (res.success) {

					}
					if (res.error) {
						console.log('Response [Error]: ' + JSON.stringify(resposeData));
						res.error.forEach((error) => {
						});
					}
					resolve(resposeData);
				}, (error: any) => {
					console.log('[Request Error]: ' + error);
					reject(error);
				});
		});
		return promise;
	}
}



