import { Component, OnInit, ViewChild } from '@angular/core';
import {ColorPickerDirective, ColorPickerService, Rgba} from 'angular2-color-picker';
import * as _         from 'lodash';
//COMPONENTS
import { ColumnsComponent }   from '../../../../components/basic/columns/columns.component';
import { ChipsComponent }     from '../../../../components/basic/chips/chips.component';
import { SearchBarComponent } from '../../../../components/basic/search-bar/search-bar.component';
//PROVIDERS
import { DataSectionsService } from '../../../../providers/sections/data-sections.service';
// SERVICES
import { SectionsService }     from '../../../../providers/sections/sections.service';


@Component({
  selector: 'app-service-categories',
  templateUrl: './service-categories.component.html',
  styleUrls: ['./service-categories.component.scss']
})
export class ServiceCategoriesComponent implements OnInit {
  @ViewChild(SearchBarComponent) private searchBarComponent: SearchBarComponent;
  @ViewChild(ColumnsComponent)   private columnsComponent  : ColumnsComponent;
  @ViewChild(ChipsComponent)     private chipsComponent    : ChipsComponent;

	objectSection:any;
  categories:Array<any>;

	dataColumns:any;
	newCategory:any;

	dataChips:any;

  reorderItems:Array<any>;
  enableAddCategory:boolean = false;


  constructor(
    private dataSectionsService:DataSectionsService,
    private sectionsService:SectionsService) {
			this.objectSection = this.dataSectionsService.getTemporarySection();
			this.categories = this.dataSectionsService.getCatalogCategories();
		}

  ngOnInit() {
		if(!this.categories){
				this.getCatalogCategoriesService();
		}else{
			this.validateCategoriesOfSection();
		}
  }

	validateCategoriesOfSection(){
		console.log("==>> EN validateCategoriesOfSection.... " + JSON.stringify(this.objectSection.categorias));

		let dataColumns:Array<any> = [];
		let categories = _.cloneDeep(this.categories);

		categories.forEach(category => {
			this.objectSection.categorias.forEach(categoryOfSection => {
				if(category.idCategoria === categoryOfSection.idCategoria){
					category.checkItem = true;
				}
			});
		});

		this.setDataChips(this.objectSection.categorias);
		this.setDataColumns(this.formattedItems(categories, 4));
	}


	setDataChips(items:any){
		this.dataChips = _.cloneDeep(items);
	}

	setDataColumns(items:any){
		this.dataColumns = _.cloneDeep(items);
	}


  ngOnChange(){ }

  ngAfterViewInit() { }

  ngAfterViewChecked() { }



	// METODOS PARA EL COMPONENTE 'search-bar'
  searchItems(value:any){
		let categoriesSearch:Array<any> = [];
		let categories = _.cloneDeep(this.categories);

		if(value && value.trim() != ''){
			categories.forEach(category => {
				if(category.nombre.toLowerCase().indexOf(value.toLowerCase()) > -1){
					category.cssSelect = true;
				}else{
					category.cssSelect = false;
				}
				categoriesSearch.push(category);
			});
		}else{
			categories.forEach(category => {
				category.cssSelect = false;
				categoriesSearch.push(category);
			});
		}

		this.setDataColumns(this.formattedItems(categoriesSearch, 4));
  }

	// METODOS PARA EL COMPONENTE 'chips'
	deletedChip(item:any){
		this.deleteCategoryToSection(item).then(success => {
			this.validateCategoriesOfSection();
		});
	}

	reorderChips(items:any){
		items.forEach(item => {
			item.prioridad = items.indexOf(item);
		});
		this.objectSection.categorias = items;
		console.log("==>> REORDENAMIENTO DE \"CHIPS\": " + JSON.stringify(items));
	}


	// METODOS PARA EL COMPONENTE 'columns'
  selectedItem(item:any){
		this.addCategoryToSection(item).then(success => {
			this.validateCategoriesOfSection();
		});
  }

	// METODOS PARA EL COMPONENTE 'table-add-feature'
	activedAddCategoryComponent(){
		this.enableAddCategory = !this.enableAddCategory;
		this.newCategory = this.dataSectionsService.getNewCategory();
	}
	saveNewCategory(item:any){

		this.sectionsService.addNewCategoryToCatalog(item).then(catalog => {
			this.addCategoryToSection(item).then(success => {
				this.enableAddCategory = !this.enableAddCategory;
				this.getCatalogCategoriesService(item);
			});
		});
  }
  cancelNewCategory(){
		this.enableAddCategory = !this.enableAddCategory;
  }


	// METODOS DE ESTE COMPONENTE

	getCatalogCategoriesService(newItem?:any){
		this.sectionsService.getCatalogCategories().then(catalog => {
			//TODO ELIMINAR ESTA CONDICION PARA PRODUCCION
			if(newItem){
				newItem.idCategoria = catalog.length;
				catalog.push(newItem);
			}
			this.dataSectionsService.setCatalogCategories(catalog);
			this.categories = this.dataSectionsService.getCatalogCategories();

			this.validateCategoriesOfSection();
		});
	}

  formattedItems(items:any, columns:number){
    let total = items.length;
    let itemsByColumn = Math.round(total/columns);
    let arrayTemp = [];
    let arrayFinal = [];

    for (let sec = 0; sec < items.length; sec++) {
      arrayTemp.push(items[sec]);

      if (arrayTemp.length === itemsByColumn || (sec + 1) == items.length) {
        arrayFinal.push({ items: arrayTemp });
        arrayTemp = [];
      }
    }

    return arrayFinal;
  }

	addCategoryToSection(category:any):Promise<any>{
		return Promise.resolve(this.objectSection.categorias.push(category));
	}

	deleteCategoryToSection(item:any):Promise<any>{
		let deletePromise = new Promise((resolve, reject) => {
			this.objectSection.categorias.forEach( category => {
				if(item.idCategoria === category.idCategoria)
				resolve(this.objectSection.categorias.splice(this.objectSection.categorias.indexOf(category), 1));
			});
		});

		return deletePromise;
	}



  // GUARDS
  canDeactivate(): Promise<boolean> | boolean {
    console.log("==>> canDeactivate: ");
    this.dataSectionsService.setStateData(2,this.validateObjectCategories());
    return true;
  }

	validateObjectCategories():boolean{
		let isValid:boolean = true;
		if(!this.objectSection.categorias.length){
				isValid = false;
			}

			return isValid;
	}

}



