import { Component, OnInit, EventEmitter, Output, Input } from '@angular/core';
import * as _         from 'lodash';

@Component({
  selector: 'component-table-categories-informative',
  templateUrl: './table-categories-informative.component.html',
  styleUrls: ['./table-categories-informative.component.scss']
})
export class TableCategoriesInformativeComponent implements OnInit {
	@Input('data') rows: Array<any>;
	@Output() outEditElement    = new EventEmitter<any>();
	@Output() outDeleteElement  = new EventEmitter<any>();

	columns = [
		{ name: '#', prop:'$$index'},
		{ name: 'nombre' },
		{ name: 'descripcion', sortable: false },
		{ name: 'Imagen', sortable: false },
	];
	messages = {
	  emptyMessage: 'Sin datos',
	  totalMessage: 'total'
	};


	constructor() { }

	ngOnInit() {	}


	editElement(item:any){
		let itemSelected = _.cloneDeep(item);
		this.desableRows();
		this.changeRowSelected(item.idInformacion);
		this.outEditElement.emit(itemSelected);
	}

	deleteElement(id){
		this.outDeleteElement.emit(id);
	}


	// CHANGE CSS
	changeRowSelected(id) {
		this.rows.forEach(row =>{
			if(id === row.idInformacion){
				row.cssSelect = true
			}else{
				row.cssSelect = false
			}
		})
	}
	desableRows(){
		if(this.rows){
			this.rows.forEach(item => {
				item.enableRow = true;
			});
		}
	}
	enableRows(){
		if(this.rows){
			this.rows.forEach(item => {
				item.enableRow = false;
				item.cssSelect = false;
			});
		}
	}
	getRowClass(row) {
		let obj = {
			'row-table-selected': row.cssSelect
		}
			return obj;
	}


}



