import { ElementBase } from './element-base';

export class DropdownElement extends ElementBase<String> {
	controlType = 'dropdown';
	options: {key: string, value: string}[] = [];

	constructor(options: {} = {}) {
		super(options);
		this.options = options['options'] || [];
	}
}



