import { Component, OnInit, EventEmitter, Output, Input } from '@angular/core';

@Component({
  selector: 'component-table-add-category',
  templateUrl: './table-add-category.component.html',
  styleUrls: ['./table-add-category.component.scss']
})
export class TableAddCategoryComponent implements OnInit {
	@Input('data') row: any;
  @Output() outSaveCategory   = new EventEmitter<any>();
  @Output() outCancelCategory = new EventEmitter<any>();

  constructor() { }

  ngOnInit() {
  }

  saveNewFeature(){
    this.outSaveCategory.emit(this.row);
  }

  deleteNewFeature(){
    this.outCancelCategory.emit();
  }

  setImage(img:any){
    this.row.estilo.icono = img;
  }

}



