import { Component, OnInit, ViewChild } from '@angular/core';
import { ImageUploadComponent }         from '../../../../components/basic/image-upload/image-upload.component';
import * as _                           from 'lodash';
declare var google; 

@Component({
  selector: 'app-profile-service',
  templateUrl: './profile-service.component.html',
  styleUrls: ['./profile-service.component.scss']
})

export class ProfileServiceComponent implements OnInit {
	@ViewChild(ImageUploadComponent) protected imageUpload: ImageUploadComponent;
	enableBoxImages:boolean = true;
	mapZoom:number = 16;
	objetoServicio: any = {
		nombre:'', 
		direccion:'', 
		coords:{lat:20.5889544, lng:-100.3896705},
		descripcion:'',
		imagenes: []
	};
	optionsFroalaEditor: any = {
		pluginsEnabled: ['align', 'charCounter', 'codeBeautifier', 'codeView', 'colors', 'draggable', 'embedly', 'emoticons', 'entities', 'file', 'fontFamily', 'fontSize', 'fullscreen', 'image', 'imageManager', 'inlineStyle', 'lineBreaker', 'link', 'lists', 'paragraphFormat', 'paragraphStyle', /*'quickInsert',*/ 'quote', 'save', 'table', 'url', 'video', 'wordPaste'],
		quickInsertButtons:[],
		placeholderText: 'P.ej. Breve descripcin del evento o actividad, directores, actores, etc.',
		toolbarButtons: ['bold', 'italic', 'underline', 'color', '|', 'align', 'formatOL', 'formatUL', '|', 'insertLink', 'insertImage' ],
	};

	constructor() {
		this.construirImagenes();
	}

	ngOnInit() {}

	ngAfterViewInit() {}

	dragEndEmit(event?){
		
		let geocoder = new google.maps.Geocoder;
		geocoder.geocode({'location': event ? event.coords : this.objetoServicio.coords}, (results, status) => {
			if (status === 'OK') {
			  if (results[1]) {
				let direccionFormateada = results[1].formatted_address; 
				console.log("==>>EVENT MARKER " + JSON.stringify(direccionFormateada));
				this.objetoServicio.direccion  = direccionFormateada;
				this.objetoServicio.coords.lat = event.coords.lat;
				this.objetoServicio.coords.lng = event.coords.lng;
			  } else {
				console.log('No results found');
			  }
			} else {
				console.log('Geocoder failed due to: ' + status);
			}
		  });
	}

	construirImagenes(){
		for(let i = 0; i < 6; i++){
			let newImage = {idImagen: i, nombre: '', tipoContenido: '', url:'', prioridad: null, esMiniatura: false};

			if(!this.objetoServicio.imagenes[i]){
				this.objetoServicio.imagenes.push(newImage);
			}else{
				this.objetoServicio.imagenes[i].idImagen = i;
			}
		}
	}

	setImage(event){
		_.remove(this.objetoServicio.imagenes, (item) => {return (!item.url);});
		this.objetoServicio.imagenes.push(event);
		this.construirImagenes();
	}

	deleteImage(event){
		this.objetoServicio.imagenes.splice(this.objetoServicio.imagenes.indexOf(event), 1);
		this.construirImagenes();
	}
}



