import { Component, OnInit, ViewChild } from '@angular/core';
import { NvD3Module } from 'angular2-nvd3';
import * as d3        from 'd3';
// COMPONENTS
import { PieComponent }      from '../../../components/basic/charts/pie/pie.component';
import { SunburstComponent } from '../../../components/basic/charts/sunburst/sunburst.component';
// SERVICES
import { ReportsService } from '../../../providers/reports/reports.service';

@Component({
  selector: 'app-data-user',
  templateUrl: './data-user.component.html',
  styleUrls: ['./data-user.component.scss']
})
export class DataUserComponent implements OnInit {
	@ViewChild(PieComponent)      private pieComponent: PieComponent;
	@ViewChild(SunburstComponent) private sunburstComponent: SunburstComponent;

	navs:Array<any> = [
    {id:0, name:'Acciones de usuario',     subNav:'actions',     viewState: false, state: null},
    {id:1, name:'Calificaciones',          subNav:'ratings',     viewState: false, state: null},
    {id:2, name:'Favoritos',               subNav:'favorites',   viewState: false, state: null},
    {id:3, name:'Agregados a itinerarios', subNav:'itineraries', viewState: false, state: null},
    {id:4, name:'Comentarios',             subNav:'comments',    viewState: false, state: null}
	  ];

	// SUNBURST CHART 
	dataChartActionsUser:any;
	paramsChartActionsUser:any = {
		id:'ChartActionsUser',
		title:'Acciones de Usuario',
		size: 500
	};

	// PIE CHART
	dataChartItineraries:any;
	paramsChartItineraries:any = {
		id:'ChartItineraries',
		title:'Itinerarios',
		mostrarNombre: false,
		size:300,
		topLegends: 210
	};

	dataChartFavorites:any;
	paramsChartFavorites:any = {
		id:'ChartFavorites',
		title:'Favoritos',
		mostrarNombre: false,
		size:300,
		topLegends: 210
	};

	dataChartComments:any;
	paramsChartComments:any = {
		id:'ChartComments',
		title:'Comentarios',
		mostrarNombre: false,
		size:300,
		topLegends: 210
	};

	dataChartRating:any;
	paramsChartRating:any = {
		id:'ChartRating',
		title:'Calificaciones',
		mostrarNombre: false,
		size:500,
		topLegends: 230
	};

	// CARDS
	cardCalifications:any = {};
	cards:Array<any> = [];

  constructor(
		private reportsService:ReportsService) { }

  ngOnInit() {
	  
	  	// CHARTS
		this.getChartActionsUser();
		this.getChartItineraries();
		this.getChartFavorites();
		this.getChartComments();
		this.getChartCalifications();

		// CARDS
		this.getCardCalifications();
		this.getCardFavorites();
		this.getCardItineraries();
		this.getCardComments();
  }
	// SUNBURST CHART
	getChartActionsUser(){
		this.reportsService.getChartActionsUser().then(
			success => {
				this.dataChartActionsUser = success;
			}
		);
	}

	// PIE CHART
	getChartItineraries(){
		this.reportsService.getChartItineraries().then(
			success => {
				this.dataChartItineraries = success.data;
			}
		);
	}
	getChartFavorites(){
		this.reportsService.getChartFavorites().then(
			success => {
				this.dataChartFavorites = success.data;
			}
		);
	}
	getChartComments(){
		this.reportsService.getChartComments().then(
			success => {
				this.dataChartComments = success.data;
			}
		);
	}
	getChartCalifications(){
		this.reportsService.getChartCalifications().then(
			success => {
				this.dataChartRating = success.data;
			}
		);
	}

	// CARDS
	getCardCalifications(){
		this.reportsService.getCardCalifications().then(
			success => {
				this.cardCalifications = success;
			}
		);
	}
	getCardFavorites(){
		this.reportsService.getCardFavorites().then(
			success => {
				this.cards.push(success);
				// this.cardFavorites = success;
			}
		);
	}
	getCardItineraries(){
		this.reportsService.getCardItineraries().then(
			success => {
				this.cards.push(success);
			}
		);
	}
	getCardComments(){
		this.reportsService.getCardComments().then(
			success => {
				this.cards.push(success);
			}
		);
	}

}



