import { Component, OnInit, EventEmitter, Output, Input } from '@angular/core';
import * as _         from 'lodash';

@Component({
  selector: 'component-table-add-feature',
  templateUrl: './table-add-feature.component.html',
  styleUrls: ['./table-add-feature.component.scss']
})
export class TableAddFeatureComponent implements OnInit {
  @Input('data') row: any;
  @Input('flag') flag: string;
  @Input('catalogOptions') selectOptions: Array<any>;
  @Output() outSaveFeature   = new EventEmitter<any>();
  @Output() outCancelFeature = new EventEmitter<any>();

	rowCopy:any;
  columns = [
    { name: 'Nombre'},
    { name: 'Tipo'},
    { name: 'Para vista'},
    { name: 'Para filtrar'},
    { name: 'cono'}
  ];


  constructor() {}

  ngOnInit() {
		this.rowCopy = _.cloneDeep(this.row);
	}

  saveNewFeature(){
    this.outSaveFeature.emit({data:this.row, flag:this.flag});
  }

  cancelNewFeature(){
    this.outCancelFeature.emit();
  }

  setImage(img:any){
    this.row.imagen = img;
  }

	changeData():boolean{
		return (_.isEqual(this.row, this.rowCopy));
	}


}



