import { Component, OnInit, EventEmitter, Output, Input } from '@angular/core';

@Component({
  selector: 'component-chips',
  templateUrl: './chips.component.html',
  styleUrls: ['./chips.component.scss']
})
export class ChipsComponent implements OnInit {
	@Input('data') chips:Array<any>;
  @Output() outDeletedItem  = new EventEmitter<any>();
  @Output() outReorderItems = new EventEmitter<any>();

  // chips:Array<any> = [];
  enableDeleteChip:boolean = true;

  constructor() { }

  ngOnInit() { }

  setChips(items:any){
    this.chips = items;
  }

  addChip(item:any){
    this.chips.push(item);
  }

  deleteChip(item:any){
    // this.chips.splice(this.chips.indexOf(item), 1);
    this.outDeletedItem.emit(item);
  }

  reorderItems(){
    this.outReorderItems.emit(this.chips);
  }

}



