import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { Subject }    from 'rxjs/Subject';
import * as _         from 'lodash';

@Injectable()
export class DataSectionsService {

	// OBJETO SECCION TEMPORAL
	temporarySection:any;
	reorderCatalogSections:Array<any>;
	categoriesSectionInformative:Array<any> = [];
	// CATALOGOS
  catalogSections:Array<any>;
	catalogOptionsMetadata:Array<any>;
	catalogCategories:Array<any>;
	// OBSERVABLES
  isCompleteSection = new Subject<any>();
	stateData         = new Subject<any>();
	// FLAGS
	pendingSection:boolean = false;
	reorderSections:boolean = false;
	isEdition:boolean;
	isEditionOfNew:boolean;
	isInformative:boolean; //==>> CONTROL PARA SECCION INFORMATIVA
	// NUEVO OBJETO SECCION
	newSection  = {
		id:null,
		numeroServicios : null,
		prioridad : null,
		activa : null,
		esNew : true,
		idCategoria : null,
		nombre : '',
		descripcion : '',
		imagen : '',
		nombreComponenete : '',
		estilo : { idEstilo: null, clase: '', clase2: '', icono: ''},
		esEvento : false,
		esInformativa : null,
		metadatos : [],
		categorias : [],
	}
	//NUEVO OBJETO METADATO
	newFeature = {
		idTipoMetadato : null,
		propiedad : '',
		imagen : '',
		prioridad : null,
		tipoValor : {
			id : null,
		  descripcion : '',
		  parseador : null,
		},
		opciones : null,
		esVisible : false,
		esfiltrable : false,
		cssSelect : false,
		enableRow : false
	};
	//NUEVO OBJETO CATEGORIA
	newCategory = {
		activa : false,
		idCategoria : null,
		nombre : '',
		descripcion : '',
		prioridad : null,
		estilo : { idEstilo: null, clase: '', clase2: '', icono: ''},
		cssSelect : false,
		checkItem : false
	};
	//NUEVO OBJETO CATEGORIA INFORMATIVA
	newCategoryInformative = {
		idInformacion : null,
		titulo : '',
		descripcion : '',
		categoria : {
			idCategoria: null,
			nombre: '',
			descripcion: ''
			// metadato: {
			// 	idTipoMetadato : null,
			// 	propiedad : '',
			// 	imagen : '',
			// 	prioridad : null,
			// 	tipoValor : { id : null, descripcion : '', parseador : null},
			// 	opciones : null,
			// 	esVisible : false,
			// 	esfiltrable : false,
			// 	cssSelect : false,
			// 	enableRow : false
			// }
		},
		imagenes : [],
		cssSelect : false,
		enableRow : false
	};
	//NUEVO OBJETO IMAGEN CATEGORIA
	newImage = {
		idImagen : null,
		nombre : '',
		tipoContenido : '',
		url : '',
		prioridad : null,
		esMiniatura : false
	};



  constructor() {}

	// CATALOGOS
	setCatalogSections(sections:any){
    this.catalogSections = sections;
  }
  getCatalogSections(){
    return _.cloneDeep(this.catalogSections);
  }

	setCatalogOptionsMetadata(catalog:any){
		this.catalogOptionsMetadata = catalog;
	}
	getCatalogOptionsMetadata(){
		return _.cloneDeep(this.catalogOptionsMetadata);
	}

	setCatalogCategories(catalog:any){
    this.catalogCategories = catalog;
		console.log("setCatalogCategories " + JSON.stringify(catalog));
  }
  getCatalogCategories(){
    return _.cloneDeep(this.catalogCategories);
  }

	// METODOS OBSERVABLES
	// OBSERVABLE IS 'COMPLETE SECTION'
  setIsCompleteSection(state:boolean) {
			this.pendingSection = !state;
      this.isCompleteSection.next(state);
  }
  getIsCompleteSection(): Observable<any> {
      return this.isCompleteSection.asObservable();
  }
	// OBSERVABLES PARA EL ESTADO DE LAS SUB RUTAS (NAVS DE 'ADD SECTION')
  setStateData(id:number, state:boolean) {
      this.stateData.next({id:id, state:state});
  }
  getStateData(): Observable<any> {
      return this.stateData.asObservable();
  }

	// GETTERS & SETTERS
	setIsInformative(flag:boolean){
		this.isInformative = flag;
	}
	setTemporarySection(section?:any){
		if(section && !section.esNew){
			this.temporarySection = section;
			this.isEdition = true;
			this.isEditionOfNew = false;
		}else if(section && section.esNew){
			this.isEdition = true;
			this.isEditionOfNew = true;
		}else if(!section){
			this.isEdition = false;
			this.isEditionOfNew = false;
			let newSection = _.cloneDeep(this.newSection);
			newSection.esInformativa = this.isInformative;
			this.temporarySection = newSection;

			console.log("==>> EN setTemporarySection: "+ JSON.stringify(this.temporarySection));
		}
	}
	getTemporarySection(){
		 return this.temporarySection;
	}

	getReordingSections(){
		return this.reorderCatalogSections;
	}
	setReordingSection(sections:any){
		this.reorderSections = true;
		this.reorderCatalogSections = sections;
	}

	getNewFeature(){
		return _.cloneDeep(this.newFeature);
	}
	getNewCategory(){
		return _.cloneDeep(this.newCategory);
	}

	getIsEdition():boolean{
		return this.isEdition;
	}
	getIsEditionOfNew():boolean{
		return this.isEditionOfNew;
	}
	isTherePendingSection():boolean{
		return this.pendingSection;
	}

	isThereReordingSections():boolean{
		return this.reorderSections;
	}
	getCategoriesSectionInformative(){
		return this.categoriesSectionInformative;
	}
	getNewCategoryInformative(){
		return _.cloneDeep(this.newCategoryInformative);
	}

	cleanData(){
		this.temporarySection = null;
		this.reorderCatalogSections = [];
		this.categoriesSectionInformative = [];
		this.pendingSection = false;
		this.reorderSections = false;
		this.isEdition = undefined;
		this.isEditionOfNew = undefined;


		// // OBJETO SECCION TEMPORAL
		// temporarySection:any;
		// reorderCatalogSections:Array<any>;
		// categoriesSectionInformative:Array<any> = [];
		// // CATALOGOS
	  // catalogSections:Array<any>;
		// catalogOptionsMetadata:Array<any>;
		// catalogCategories:Array<any>;
		// // OBSERVABLES
	  // isCompleteSection = new Subject<any>();
		// stateData         = new Subject<any>();
		// // FLAGS
		// pendingSection:boolean = false;
		// reorderSections:boolean = false;
		// isEdition:boolean;
		// isEditionOfNew:boolean;
		// isInformative:boolean;
	}


	// METODOS PARA FORMATEO DE DATOS
	addNewSectionToCatalog(){
		let sections = _.cloneDeep(this.catalogSections);
		let newSection = _.cloneDeep(this.newSection);
		newSection.esInformativa = this.isInformative;
		sections.push(newSection);

		return sections;
	}










//METODOS MOCK
	changeViewSectionData(section:any, sections:any){
		sections.forEach(sec => {
			if(sec.id === section.id){
				sec.activa = !sec.activa;
			}
		});

		return sections;
	}

	deleteSectionData(section:any, sections:any){
		sections.splice(sections.indexOf(section), 1)
		return sections;
	}


}



