import { Component, OnInit, ViewChild } from '@angular/core';
import { NvD3Module } from 'angular2-nvd3';
import * as d3        from 'd3';
// COMPONENTS
import { PieComponent }      from '../../../components/basic/charts/pie/pie.component';
import { SunburstComponent } from '../../../components/basic/charts/sunburst/sunburst.component';
// SERVICES
import { ReportsService } from '../../../providers/reports/reports.service';
import { ActivatedRoute } from '@angular/router';
import { Observable } from 'rxjs';
import { Subscription }     from 'rxjs/Subscription';


@Component({
  selector: 'app-data-app',
  templateUrl: './data-app.component.html',
  styleUrls: ['./data-app.component.scss']
})
export class DataAppComponent implements OnInit {
	@ViewChild(PieComponent)      private pieComponent: PieComponent;
	@ViewChild(SunburstComponent) private sunburstComponent: SunburstComponent;
  subscription1:Subscription;
  subscription2:Subscription;
	navs:Array<any> = [
    {id:0, name:'Secciones',       subNav:'sections',   viewState: false, state: null},
    {id:1, name:'Categorias',      subNav:'categories', viewState: false, state: null},
    {id:2, name:'Caractersticas', subNav:'features',   viewState: false, state: null}
  ];

  // SUNBURST CHART 
	dataChartViews:any;
	paramsChartViews:any = {
		id:'ChartViews',
		title:'Vistas',
		size: 500
	};

	// PIE CHART
	dataChartContents:any;
	paramsChartContents:any = {
		id:'ChartContents',
		title:'Contenidos',
		mostrarNombre: false,
		size:500,
		topLegends: 230
  };

  // CARDS
  cards:Array<any> = [];
  
  constructor(
    private reportsService:ReportsService,
    private route: ActivatedRoute) { 
      const url: Observable<string> = route.url.map(segments => segments.join(''));
      const url2: Observable<string> = route.parent.url.map(segments => segments.join(''));

      this.subscription2 = url2.subscribe(x => {console.log("parent url: " + x)});
      this.subscription1 = url.subscribe(x  => {console.log("child url:  " + x)});

    }

  ngOnInit() {

    // CHARTS
    this.getChartViews();
    this.getChartContents();

    // CARDS
    this.getCardViews();
    this.getCardContents();
  }

  // SUNBURST CHART
  getChartViews(){
    this.reportsService.getChartViews().then(
      success => {
        this.dataChartViews = success;
      }
    );
  }

  // PIE CHART
  getChartContents(){
    this.reportsService.getChartContents()
		.subscribe(
			success => {
        this.dataChartContents = success.data ? success.data.data : [];
      },
      error => {}
		);

    // this.reportsService.getChartContents().then(
    //   success => {
    //     this.dataChartContents = success.data;
    //   }
    // );
  }

  // CARDS
  getCardViews(){
    this.reportsService.getCardViews().then(
      success => {
        this.cards.push(success);
      }
    );
  }
  getCardContents(){
    this.reportsService.getCardContents().then(
      success => {
        this.cards.push(success);
      }
    );
  }

}



