import { Injectable } from '@angular/core';
import {
        HttpEvent, 
        HttpInterceptor, 
        HttpHandler, 
        HttpRequest, 
        HttpResponse, 
        HttpErrorResponse
        }             from '@angular/common/http';
import { Storage } from './providers/commons/storage.provider';
import { Observable } from 'rxjs';

// import 'rxjs/add/operator/do';
// import 'rxjs/add/operator/catch';
// import 'rxjs/add/observable/throw';



@Injectable()
export class Interceptor implements HttpInterceptor {

  constructor(
    private storage:Storage){}

  intercept(req: HttpRequest<any>, next: HttpHandler): Observable<HttpEvent<any>> {

      const authReq = req.clone({setHeaders: {Authorization: '1234567890'}});

      console.log("==>> EN INTERCEPTOR... REQUEST:" + JSON.stringify(req));


    return next.handle(authReq).do(event => {

        if (event instanceof HttpResponse) {
          console.log('==>> EN INTERCEPTOR... RESPONSE:', JSON.stringify(event));
          if(event.body.error){
            event.body.error.forEach(element => {
              this.storage.setDialog(element);
            });
            
            console.log('==>> EN INTERCEPTOR... RESPONSE_ERROR_CLIENT:'+ JSON.stringify(event.body.error[0]));
          }
        }
        
      }).catch((error: any) => {
        if (error instanceof HttpErrorResponse) {
            this.storage.setDialog(error);
          console.log('==>> EN INTERCEPTOR... RESPONSE_ERROR_SERVER:' + JSON.stringify(error));
        } else {
          return Observable.throw(error);
        }
      });

  }

  


}


