import { Component, OnInit, EventEmitter, Output, Input } from '@angular/core';
import { ImagetoBase64 }     from '../../../providers/commons/imagetoBase64.provider';
import * as _                from 'lodash';

@Component({
  selector: 'app-image-upload-circle',
  templateUrl: './image-upload-circle.component.html',
  styleUrls: ['./image-upload-circle.component.scss']
})
export class ImageUploadCircleComponent implements OnInit {
  @Input('data') path: any;
  @Output() outAddImage = new EventEmitter<any>();
	textInfo = {size:'90px x 90px', type:'.jpeg, .png'};
	file: File;
  enableCenterConatiner:boolean = false;

  constructor(private filetoBase64: ImagetoBase64) {}

  ngOnInit() { }

  selectFile($event): void {
  	const inputValue = $event.target;
  	this.file = inputValue.files.length > 0 ? inputValue.files[0] : null;

  	if (this.file) {
  	  this.filetoBase64.convertFile(this.file).then(
  		response => {
  		  	if (response != null) {
  				this.path = 'data:image/png;base64,' + response;
          this.outAddImage.emit(this.path);
  			}
  		});
  	}
  }

  ngAfterViewInit(){
  }

  changeStyleConatiner(state:boolean){
    this.enableCenterConatiner = state;
  }


}



