import { Injectable } from '@angular/core';
import { CanDeactivate, ActivatedRouteSnapshot, RouterStateSnapshot } from '@angular/router';
import { Observable } from 'rxjs/Observable';
//Components
import { ServiceFeaturesComponent }     from '../modules/sections/add-section-service/service-features/service-features.component';


@Injectable()
export class TestCanDeactivateGuard implements CanDeactivate<any> {
  canDeactivate(
    component: any,
    route: ActivatedRouteSnapshot,
    state: RouterStateSnapshot
  ): Promise<boolean> | boolean {

    console.log("==>> TestCanDeactivateGuard: " );
    console.log("==>> ActivatedRouteSnapshot: "   + route);
    console.log("==>> RouterStateSnapshot: "      + state);
    return component.canDeactivate();
  }
}



