import { Component, OnInit, ViewChild, EventEmitter, Output, Input } from '@angular/core';
import { ImagetoBase64 }                from '../../../providers/commons/imagetoBase64.provider';
import * as _                           from 'lodash';
import { MaterializeAction }            from 'angular2-materialize';

import { AlertComponent }   from '../dialogs/alert/alert.component';

@Component({
  selector: 'app-image-upload',
  templateUrl: './image-upload.component.html',
  styleUrls: ['./image-upload.component.scss']
})
export class ImageUploadComponent implements OnInit {
	@Input('data') path: any;
	@Input('maxImages') maxImages: any;
	@Input('enableBoxImages') enableBoxImages: boolean;
	@Output() outAddImage    = new EventEmitter<any>();
	@Output() outDeleteImage = new EventEmitter<any>();

	@ViewChild(AlertComponent) private dialogAlert: AlertComponent;

	textInfo = {size:'90px x 90px', type:'.jpeg, .png'};
  file: File;
	images:Array<any>
	imageSelected:any;
	modalActions = new EventEmitter<string|MaterializeAction>();

	public muestraBoxSix: boolean;// ELIMINAR

  constructor(private filetoBase64: ImagetoBase64) {}


  ngOnInit() {
		if(this.enableBoxImages)this.images = this.path;
	}

  selectFile($event): void {
		const inputValue = $event.target;
    this.file = inputValue.files.length > 0 ? inputValue.files[0] : null;

    if (this.file) {

			this.filetoBase64.convertFile(this.file).then(
	        response => {
	          if (response != null) {
							if(this.images){
								let imagesx = _.cloneDeep(this.images);
								_.remove(imagesx, (item) => {return (!item.url);});
								if(imagesx.length < this.maxImages){
									this.path = 'data:image/png;base64,' + response;
									this.emitEvent();
						 		}else{
									this.path = null;
									this.dialogAlert.openModal('Slo puedes guardar ' + this.maxImages + ' imagenes');
								}
							}else{
								this.path = 'data:image/png;base64,' + response;
								this.emitEvent();
							}
						}



						});
				}
	  }

		emitEvent(){
			if(this.enableBoxImages){
				this.outAddImage.emit({idImagen: null, nombre: '', tipoContenido: '', url:this.path, prioridad: null, esMiniatura: false});
			}else{
				this.outAddImage.emit(this.path);
			}
		}


  selectImage(image: any): void {
	  if (image.url) {
			this.imageSelected = image;
		  this.path = image.url;
		  this.openModal();
	  }
  }

  deleteImage() {
		this.path = null;
		this.outDeleteImage.emit(this.imageSelected);
	  this.closeModal();
  }


	openModal() {
		this.modalActions.emit({action: 'modal', params: ['open']});
	}
  closeModal() {
	  this.modalActions.emit({action: 'modal', params: ['close']});
  }

	outAlertDialog(eve:any){
		console.log("==>> EN outAlertDialog..." + eve);
		// this.formatedData();
	}


}



