import { Component, Input, OnInit } from '@angular/core';
import { FormGroup }                from '@angular/forms';

import { ElementBase }              from '../../../models/general/dynamicForm/element-base';
import { ElementControlService }    from '../../../providers/commons/element-control.service';

@Component({
  selector: 'app-dynamic-forms',
  templateUrl: './dynamic-forms.component.html',
  styleUrls: ['./dynamic-forms.component.scss']
})
export class DynamicFormsComponent implements OnInit {
	@Input() elements: ElementBase<any>[] = [];
	form: FormGroup;
	payLoad = '';
	constructor(private elementoService: ElementControlService) { }

	ngOnInit() {
		this.form = this.elementoService.toFormGroup(this.elements);
		console.log('++++++DynamicFormsComponent - ngOnInit');
	}

	onSubmit() {
		this.payLoad = JSON.stringify(this.form.value);
	}
}



