import { BrowserModule }                                      from '@angular/platform-browser';
import { NgModule, CUSTOM_ELEMENTS_SCHEMA, NO_ERRORS_SCHEMA } from '@angular/core';
import { FormsModule, ReactiveFormsModule }                   from '@angular/forms';
// import { HttpModule }                                         from '@angular/http';

import {HttpClientModule}  from '@angular/common/http'
import {HTTP_INTERCEPTORS} from '@angular/common/http';
import { Interceptor }     from './Interceptor';

import { Storage } from './providers/commons/storage.provider';

import { AppComponent }                                       from './app.component';

// componentes de terceros
import { MaterializeModule}   from 'angular2-materialize';
import { NgxDatatableModule } from '@swimlane/ngx-datatable';
import { DndModule}           from 'ng2-dnd';
import { ColorPickerModule }  from 'angular2-color-picker';
import { NvD3Module } 				from 'angular2-nvd3';
import { AgmCoreModule } 			from '@agm/core'; // google-maps
import { FroalaEditorModule, FroalaViewModule } from 'angular2-froala-wysiwyg';
// import {nvD3} from 'ng2-nvd3';
// import { ChartsModule } from 'ng2-charts/ng2-charts';

// componentes
import { SideBarComponent }           from './components/side-bar/side-bar.component';
import { PageNotFoundComponent }      from './components/page-not-found/page-not-found.component';

// componentes basicos
import { CardComponent }              from './components/basic/card/card.component';

// modulos de routing
import { AppRoutingModule }           from './app-routing.module';
import { SectionsRoutingModule }      from './modules/sections/sections-routing.module';
import { AdminServicesRoutingModule } from './modules/admin-services/admin-services-routing.module';
import { ReportsRoutingModule }       from './modules/reports/reports-routing.module';

// Modulos
import { SectionsModule }						  from './modules/sections/sections.module';
import { AdminServicesModule }        from './modules/admin-services/admin-services.module';
import { ReportsModule }              from './modules/reports/reports.module';
import { BasicComponentsModule }      from './modules/basic-components/basic-components.module';

@NgModule({
  declarations: [
    AppComponent,
    SideBarComponent,
    PageNotFoundComponent
		// ImageUploadComponent
  ],
  imports: [
    BrowserModule,
    FormsModule,
		ReactiveFormsModule,
    // HttpModule,

    HttpClientModule,

    // componentes terceros
    MaterializeModule,
    NgxDatatableModule,
    DndModule.forRoot(),
		ColorPickerModule,
		AgmCoreModule.forRoot({apiKey: 'AIzaSyBfILXdbVGSSz53VB0UTTWBvpmCGLd_p9g'}),
		FroalaEditorModule.forRoot(),
		FroalaViewModule.forRoot(),
		NvD3Module,
		// ChartsModule,
    // modulos routing
    SectionsRoutingModule,
		AdminServicesRoutingModule,
		ReportsRoutingModule,
    AppRoutingModule,
    // modulos
    SectionsModule,
		AdminServicesModule,
    ReportsModule,
    BasicComponentsModule
  ],
  exports: [
    BrowserModule,
    FormsModule,
		ReactiveFormsModule,
    // HttpModule,

    HttpClientModule,
    
		// componentes basicos
		// ImageUploadComponent,
    // componentes terceros
    MaterializeModule,
    NgxDatatableModule,
    DndModule,
		ColorPickerModule,
		AgmCoreModule,
		FroalaEditorModule,
		FroalaViewModule,
		NvD3Module
  ],
  schemas: [ CUSTOM_ELEMENTS_SCHEMA, NO_ERRORS_SCHEMA ],
  providers: [{
    provide: HTTP_INTERCEPTORS,
    useClass: Interceptor,
    multi: true,
    },
    Storage
  ],
  bootstrap: [AppComponent]
})
export class AppModule {}



