import { Pipe, PipeTransform } from '@angular/core';
import * as _                  from 'lodash';

@Pipe({
  name: 'isInformative',
  pure: false
})
export class IsInformativePipe implements PipeTransform {

  transform(items: any[], isInformative?: boolean): any[] {
    return _.filter(items, (item) => { return (item.esInformativa === isInformative); });
  }

}



