import { Component, OnInit, ViewChild } from '@angular/core';
import * as _ from 'lodash';
// COMPONENTS
import { TableComponent }               from '../../../../components/basic/table/table.component';
import { TableAddFeatureComponent }     from '../../../../components/basic/table-add-feature/table-add-feature.component';
// PROVIDERS
import { DataSectionsService }          from '../../../../providers/sections/data-sections.service';

@Component({
  selector: 'app-service-features',
  templateUrl: './service-features.component.html',
  styleUrls: ['./service-features.component.scss']
})
export class ServiceFeaturesComponent implements OnInit {
  @ViewChild(TableComponent) tableComponent: TableComponent;
  @ViewChild(TableAddFeatureComponent) tableAddFeatureComponent: TableAddFeatureComponent;

	objectSection:any = {};
  enableAddFeature:boolean = false;
	rows:any;
  row:any;
  flagTableFeature:string = '';
	catalogOptions:Array<any>;

  constructor(
    private dataSectionsService:DataSectionsService) {
			this.objectSection  = this.dataSectionsService.getTemporarySection();
			this.catalogOptions = this.dataSectionsService.getCatalogOptionsMetadata();
		}

  ngOnInit() {
		this.setDataTable();
	}

  ngAfterViewInit() { }

	// METODOS PARA EL COMPONENTE 'table'
	setDataTable(){
		this.rows = _.cloneDeep(this.objectSection.metadatos);
	}

  editFeauture(item:any){
    this.activedTableAddFeatureComponent('EDIT', item);
  }

  deleteFeature(item:any){
    this.deleteItem(item);
  }


	// METODOS PARA EL COMPONENTE 'table-add-feature'
  activedTableAddFeatureComponent(flag:string, item?:any){
		this.row = item ? item : this.dataSectionsService.getNewFeature();
		this.flagTableFeature = flag;
    this.enableAddFeature = !this.enableAddFeature;
    this.tableComponent.desableRows();
  }

  saveFeature(item:any){
    if(item.flag === 'EDIT'){
      this.overwriteRow(item.data);
    }else if(item.flag === 'NEW'){
      this.addRow(item.data);
    }
	   this.enableAddFeature = !this.enableAddFeature;
	   this.tableComponent.enableRows();
  }

  cancelFeature(item:any){
    this.enableAddFeature = !this.enableAddFeature;
    this.tableComponent.enableRows();
  }


	// METODOS DE ESTE COMPONENTE
	addRow(item:any){
    item.idTipoMetadato = this.objectSection.metadatos.length ? this.objectSection.metadatos.length : 0;
    this.objectSection.metadatos.push(item);

		this.setDataTable();
	}

	deleteItem(id) {
		this.objectSection.metadatos.forEach(row => {
			if(id === row.idTipoMetadato)
			this.objectSection.metadatos.splice(this.objectSection.metadatos.indexOf(row), 1);
		});

		this.setDataTable();
  }

	overwriteRow(item:any){
		this.objectSection.metadatos.forEach(row => {
			if(item.idTipoMetadato === row.idTipoMetadato)
			this.objectSection.metadatos.splice(this.objectSection.metadatos.indexOf(row), 1, item);
		});

		this.setDataTable();
	}

	// GUARDS
  canDeactivate(): Promise<boolean> | boolean {
    console.log("==>> canDeactivate: ");

		this.tableComponent.enableRows();
    this.dataSectionsService.setStateData(1,this.validateObjectFeatures());
    return true;
  }

	validateObjectFeatures():boolean{
		let isValid:boolean = true;
		if(!this.objectSection.metadatos.length){
				isValid = false;
			}

			return isValid;
	}

}



