import { Component, OnInit, ViewChild } from '@angular/core';
import * as _ from 'lodash';
// COMPONENTS
import { TableCategoriesInformativeComponent }  from '../../../../components/basic/table-categories-informative/table-categories-informative.component';
import { TableAddCategoryInformativeComponent } from '../../../../components/basic/table-add-category-informative/table-add-category-informative.component';
//PROVIDERS
import { DataSectionsService }          from '../../../../providers/sections/data-sections.service';

@Component({
  selector: 'app-informative-categories',
  templateUrl: './informative-categories.component.html',
  styleUrls: ['./informative-categories.component.scss']
})
export class InformativeCategoriesComponent implements OnInit {
  @ViewChild(TableCategoriesInformativeComponent) tableComponent: TableCategoriesInformativeComponent;
	@ViewChild(TableAddCategoryInformativeComponent) TableAddCategoryInformativeComponent: TableAddCategoryInformativeComponent;

	dataCategories:Array<any>;
	dataTable:Array<any>;
	dataCategory:any;
	flagTableCategory:string = '';
  enableAddCategory:boolean = false;
	catalogOptions:Array<any>;

  constructor(
    private dataSectionsService:DataSectionsService) {
			this.catalogOptions = this.dataSectionsService.getCatalogOptionsMetadata();
			this.dataCategories = this.dataSectionsService.getCategoriesSectionInformative();
		}

  ngOnInit() {
		this.setDataTable();
	}


	// METODOS PARA EL COMPONENTE 'table'
	setDataTable(){
		this.dataTable = _.cloneDeep(this.dataCategories);
	}
	editCategory(category:any){
		this.activedTableAddCategoryComponent('EDIT', category);
	}
	deleteCategory(id:number){
    this.deleteItem(id);
	}

	// METODOS PATA EL COMPONENTE 'table-add-category-informative'
	activedTableAddCategoryComponent(flag:string, category?:any){
		this.dataCategory = category ? category : this.dataSectionsService.getNewCategoryInformative();
		this.flagTableCategory = flag;
	  this.enableAddCategory = !this.enableAddCategory;
    this.tableComponent.desableRows();
	}

	saveCategory(category:any){
		console.log("==>> EN SAVE CATEGORY: this.dataCategories: " + JSON.stringify(category.data));

		if(category.flag === 'NEW'){
			this.addRow(category.data);
		}else if(category.flag === 'EDIT'){
			this.overwriteRow(category.data);
		}
		this.enableAddCategory = !this.enableAddCategory;
		this.tableComponent.enableRows();
	}

	cancelCategory(category:any){
		this.enableAddCategory = !this.enableAddCategory;
		this.tableComponent.enableRows();
	}

	// METODOS DE ESTE COMPONENTE
	addRow(item:any){
    item.idInformacion = this.dataCategories.length ? this.dataCategories.length : 0;
    this.dataCategories.push(item);

		this.setDataTable();
	}

	deleteItem(id:number) {
		this.dataCategories.forEach(category => {
			if(id === category.idInformacion)
			this.dataCategories.splice(this.dataCategories.indexOf(category), 1);
		});

		this.setDataTable();
  }

	overwriteRow(item:any){
		this.dataCategories.forEach(row => {
			if(item.idInformacion === row.idInformacion)
			this.dataCategories.splice(this.dataCategories.indexOf(row), 1, item);
		});

		this.setDataTable();
	}

	// GUARDS
  canDeactivate(): Promise<boolean> | boolean {
    console.log("==>> canDeactivate: ");

		this.tableComponent.enableRows();
    this.dataSectionsService.setStateData(1,this.validateObjectCategories());
    return true;
  }

	validateObjectCategories():boolean{
		let isValid:boolean = true;
		if(!this.dataCategories.length){
				isValid = false;
			}

			return isValid;
	}

}



