import { NgModule }             from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { MainComponent }                  from './main/main.component';
import { AddSectionInformativeComponent } from './add-section-informative/add-section-informative.component';
import { ServiceFeaturesComponent }       from './add-section-service/service-features/service-features.component';
import { ServiceCategoriesComponent }     from './add-section-service/service-categories/service-categories.component';
import { ServiceProfileComponent }        from './add-section-service/service-profile/service-profile.component';

import { AddSectionServiceComponent }     from './add-section-service/add-section-service.component';
import { InformativeProfileComponent }    from './add-section-informative/informative-profile/informative-profile.component';
import { InformativeCategoriesComponent } from './add-section-informative/informative-categories/informative-categories.component';

//Guards
import { TestCanActiveGuard }           from '../../guards/test-can-active.guard';
import { TestCanDeactivateGuard }       from '../../guards/test-can-deactivate.guard';


const routes: Routes = [
  {
    path:'main-section',
    component: MainComponent,
    // canDeactivate: [TestCanDeactivateGuard]
  },
  {
    path:'add-section-service',
    component: AddSectionServiceComponent,

    canDeactivate: [TestCanDeactivateGuard],
    canActivate: [TestCanActiveGuard],
    // canActivateChild: [TestCanActiveGuard],

    children:[
    {
      path:'profile-general',
      component: ServiceProfileComponent,
      outlet: 'sub',
      canDeactivate: [TestCanDeactivateGuard]
    },
    {
      path:'features',
      component: ServiceFeaturesComponent,
      outlet: 'sub',
      canDeactivate: [TestCanDeactivateGuard]
    },
    {
      path:'categories',
      component: ServiceCategoriesComponent,
      outlet: 'sub',
      canDeactivate: [TestCanDeactivateGuard]
    }]
  },
  {
    path:'add-section-informative',
    component: AddSectionInformativeComponent,
    canDeactivate: [TestCanDeactivateGuard],
    canActivate: [TestCanActiveGuard],

    children:[
    {
      path:'profile-general',
      component: InformativeProfileComponent,
      outlet: 'sub',
			canDeactivate: [TestCanDeactivateGuard]
    },
    {
      path:'categories',
      component: InformativeCategoriesComponent,
      outlet: 'sub',
			canDeactivate: [TestCanDeactivateGuard]
    }]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class SectionsRoutingModule { }



