import { Component, OnInit, Input, EventEmitter, Output } from '@angular/core';

@Component({
  selector: 'component-card',
  templateUrl: './card.component.html',
  styleUrls: ['./card.component.scss']
})
export class CardComponent implements OnInit {
  @Input('data') dataCard: any;
  @Output() outEditItem     =  new EventEmitter<any>();
  @Output() outViewItem     =  new EventEmitter<any>();
  @Output() outDeletedItem  =  new EventEmitter<any>();

  constructor() { }

  ngOnInit() {}

  editItem(item:any){
    this.outEditItem.emit(item);
  }

  viewItem(item:any){
    this.outViewItem.emit(item);
  }

  deletedItem(item:any){
    // if(this.confirm("Deseas eliminar esta seccin?"))
    this.outDeletedItem.emit(item);
  }

  // confirm(message?: string) {
  //   return new Promise<boolean>(resolve => {
  //     return resolve(window.confirm(message || ''));
  //   });
  // };

}



