import { Injectable }          from '@angular/core';
import * as modelos            from '../../models/Models';

@Injectable()
export class ImagetoBase64 {
	convertFile(file: File): Promise<string> {
		if (file) {
			return new Promise( (resolve, reject) => {
				const reader = new FileReader();
				try {
					reader.onload = function(readerEvt: any) {
			            const binaryString = readerEvt.target.result;
						// console.log("**** Conversion: " + btoa(binaryString));
						resolve(btoa(binaryString));
		        	};
					reader.readAsBinaryString(file);
				} catch (e) {
					reject(e);
				}
			});
		}
	}
	// convertFiletoImage(file: File): modelos.Imagen {
	convertFiletoImage(file: File): any {
		if (file) {
			let imagen: modelos.Imagen = {};
			return this.convertFile(file).then(
				response => {
					if (response != null) {
						imagen.url = 'data:image/png;base64,' + response;
					}
			});
		}
	}
}



