import {Component,EventEmitter, ViewChild, OnInit} from "@angular/core"
import { Subscription }     from 'rxjs/Subscription';
// COMPONENTS
import { AlertComponent }   from './components/basic/dialogs/alert/alert.component';
// PROVIDERS
import { Storage }          from './providers/commons/storage.provider';


@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent implements OnInit {
    @ViewChild(AlertComponent) public dialogAlert: AlertComponent;
    subscription:Subscription;

    constructor(
      private storage:Storage){}

    ngOnInit() {
      this.subscription = this.storage.getDialog()
      .subscribe(eve => {
        setTimeout(() => {
         this.dialogAlert.openModal(eve.message);
        }, 500);        
      });
    }

    ngAfterViewInit(){}

    ngOnDestroy() {
      this.subscription.unsubscribe();
    }

    outAlertDialog(eve:any){}

}



