import { Component, OnInit, ViewChild }   from '@angular/core';
import { Router }       from '@angular/router';
import { Subscription } from 'rxjs/Subscription';
import { Observable }   from 'rxjs';
import { Subject }      from 'rxjs/Subject';
//COMPONENTS
import { ConfirmComponent } from '../../../components/basic/dialogs/confirm/confirm.component';
// PROVIDERS
import { DataSectionsService } from '../../../providers/sections/data-sections.service';
// SERVICES
import { SectionsService }     from '../../../providers/sections/sections.service';

@Component({
  selector: 'app-add-section-informative',
  templateUrl: './add-section-informative.component.html',
  styleUrls: ['./add-section-informative.component.scss']
})
export class AddSectionInformativeComponent implements OnInit {
	@ViewChild(ConfirmComponent) private dialogConfirm: ConfirmComponent;

	objectSection:any;
	categoriesSectionInformative:any;
	saveDataSection:boolean;
  subscription:Subscription;
  subjectFlowSection = new Subject<boolean>();
  isCompleteSection:boolean = false;
	cancelEdition:boolean = false;
	saveData:boolean = false;
  navs:Array<any> = [
    {id:0, name:'Perfil General',  subNav:'profile-general', viewState: false, state: null},
    {id:1, name:'Categorias',      subNav:'categories',      viewState: false, state: null}
  ];


  constructor(
    private dataSectionsService:DataSectionsService,
		private sectionsService:SectionsService,
		private router: Router) {
			this.objectSection = this.dataSectionsService.getTemporarySection();
			this.categoriesSectionInformative = this.dataSectionsService.getCategoriesSectionInformative();
		}

  ngOnInit() {

    this.subjectFlowSection.subscribe(state => {
      if(state && this.saveDataSection){
        this.dataSectionsService.setIsCompleteSection(false);
      }
			else if(state && !this.saveDataSection){
				this.dataSectionsService.cleanData();
				this.dataSectionsService.setIsCompleteSection(true);
      }
			else if(!state && !this.saveDataSection){
				this.cancelEdition = false;
      }
    });

    this.subscription = this.dataSectionsService.getStateData()
    .subscribe(nav => {

      let flag:boolean = true;

      this.navs.forEach(item => {
        if(nav.id === item.id){
          item.viewState = true;
          item.state = nav.state;
        }
        if(!item.state)flag = false;

      });

      if(flag)this.isCompleteSection = true;
    });

		if(!this.isCompleteSectionValidation()){
			this.navs.forEach(nav => {
				nav.viewState = true;
				nav.state = true;
			});
			this.isCompleteSection = true;
		}
  }

  ngOnDestroy() {
    this.subscription.unsubscribe();
    this.subjectFlowSection.unsubscribe();
  }
  outConfirmDialog(eve:any){
		this.subjectFlowSection.next(eve);
  }

  canDeactivate(): Observable<boolean> | Promise<boolean> | boolean {
		let isEditionOfNew = this.dataSectionsService.getIsEditionOfNew();
		let isEdition = this.dataSectionsService.getIsEdition();
		let isNewEdition = !isEditionOfNew && !isEdition;

		if((!this.isCompleteSection && (isNewEdition || isEditionOfNew) && !this.cancelEdition)
				|| (this.isCompleteSection && !this.saveData && !this.cancelEdition)){
			this.dialogConfirm.openModal('Estas en una edicin de Seccin, Deseas continuar despus?');
			this.saveDataSection = true;
			return this.subjectFlowSection.asObservable();
    }
		else if((!this.isCompleteSection && (isEdition || !isEditionOfNew) || this.cancelEdition) && !this.saveData){
      this.dialogConfirm.openModal('Si no concluyes esta edicin perderas tu informacin');
			this.saveDataSection = false;
			return this.subjectFlowSection.asObservable();
    }
    return true;
  }

	isCompleteSectionValidation():boolean{
		let isDisabled:boolean = false;

		if(	 !this.objectSection.nombre
			|| !this.objectSection.descripcion
			|| !this.objectSection.imagen
			|| (!this.objectSection.estilo.clase  ||  this.objectSection.estilo.clase  === '#fff')
			|| (!this.objectSection.estilo.clase2 ||  this.objectSection.estilo.clase2 === '#fff')
			|| !this.objectSection.estilo.icono
			|| !this.categoriesSectionInformative.length){
				isDisabled = true;
			}

			return isDisabled;
	}

	addNewSectionService(){
		let newReorderSections = this.dataSectionsService.getReordingSections();

		this.sectionsService.addNewSectionService(this.objectSection).then(success => {
			this.sectionsService.updateSectionsPriority(newReorderSections).then(success => {
				this.sectionsService.addInformativeCategories(this.categoriesSectionInformative).then(success => {
					this.dataSectionsService.setIsCompleteSection(true);
					this.dataSectionsService.cleanData();
					this.cancelEdition = false;
					this.saveData = true;
					this.router.navigate(['main-section']);
				});
			});
		});
	}

	cancelEditionSection(){
		this.cancelEdition = true;
		this.router.navigate(['']);
	}




}



