import { Injectable }         from '@angular/core';
import * as models            from '../../models/Models';
import { Observable } from 'rxjs';
import { Subject }    from 'rxjs/Subject';

@Injectable()
export class Storage {
    usuarioLS: models.Usuario;
    secciones: any;
    dialog = new Subject<any>();

    constructor() { }

    setUsuario(datos: models.Usuario) {
      this.usuarioLS = datos;
    }
    getUsuario() {
      return this.usuarioLS;
    }

    setSecciones(secciones: any) {
      this.secciones = secciones;
    }
    getSecciones() {
      return this.secciones;
    }


    setDialog(text:any){
      this.dialog.next(text);
    }
    getDialog(): Observable<any> {
      return this.dialog.asObservable();
    }


}



