import { NgModule, CUSTOM_ELEMENTS_SCHEMA, NO_ERRORS_SCHEMA } from '@angular/core';
import { FormsModule }                      from '@angular/forms';
import { CommonModule }                     from '@angular/common';

// ROUTING
import { SectionsRoutingModule } from './sections-routing.module';
// MODULES
import { BasicComponentsModule } from '../basic-components/basic-components.module';

// COMPONENTS
import { MainComponent }                  from './main/main.component';
import { AddSectionServiceComponent }     from './add-section-service/add-section-service.component';
import { AddSectionInformativeComponent } from './add-section-informative/add-section-informative.component';
import { ServiceFeaturesComponent }       from './add-section-service/service-features/service-features.component';
import { ServiceCategoriesComponent }     from './add-section-service/service-categories/service-categories.component';
import { ServiceProfileComponent }        from './add-section-service/service-profile/service-profile.component';
// PROVIDERS
import { ImagetoBase64 }       from '../../providers/commons/imagetoBase64.provider';
import { SectionsService }     from './../../providers/sections/sections.service';
import { DataSectionsService } from './../../providers/sections/data-sections.service';
// SERVICES
import { TestSectionsService } from './../../providers/sections/test-sections.service';
// import { BaseService }         from './../../providers/commons/baseService.service';

// GUARDS
import { TestCanActiveGuard }       from '../../guards/test-can-active.guard';
import { TestCanDeactivateGuard }   from '../../guards/test-can-deactivate.guard';

// import { ChartsModule } from 'ng2-charts/ng2-charts';
import { NvD3Module } from 'angular2-nvd3';
import { InformativeProfileComponent } from './add-section-informative/informative-profile/informative-profile.component';
import { InformativeCategoriesComponent } from './add-section-informative/informative-categories/informative-categories.component';

// import {nvD3} from 'ng2-nvd3';

@NgModule({
  imports: [
    CommonModule,
	  FormsModule,
  	SectionsRoutingModule,
    //modules
    BasicComponentsModule
  ],
  declarations: [
    MainComponent,
    AddSectionServiceComponent,
    ServiceFeaturesComponent,
    ServiceCategoriesComponent,
    ServiceProfileComponent,
		AddSectionInformativeComponent,
		InformativeProfileComponent,
		InformativeCategoriesComponent
	  ],

  providers: [
  // Guards
  TestCanActiveGuard,
  TestCanDeactivateGuard,
  // Custom
  ImagetoBase64,
  TestSectionsService,
  DataSectionsService,
  //Services
  SectionsService,
  // BaseService
  ],
  schemas: [ CUSTOM_ELEMENTS_SCHEMA, NO_ERRORS_SCHEMA]
})
export class SectionsModule { }



