import { Injectable }       from '@angular/core';

import { DropdownElement } from '../../models/general/dynamicForm/element-dropdown';
import { ElementBase }      from '../../models/general/dynamicForm/element-base';
import { TextboxElement }  from '../../models/general/dynamicForm/element-textbox';
// obtener los metadatos
@Injectable()
export class ElementService {
	getElements() {
		console.log('******* ElementService --> getElements');
		let elements: ElementBase<any>[] = [
			new DropdownElement({
				key: 'brave',
				label: 'Bravery Rating',
				options: [
					{key: 'solid',  value: 'Solid'},
					{key: 'great',  value: 'Great'},
					{key: 'good',   value: 'Good'},
					{key: 'unproven', value: 'Unproven'}
				],
				order: 3
			}),
			new TextboxElement({
				key: 'firstName',
				label: 'Apellido paterno',
				value: 'Angeles',
				required: true,
				order: 1
			}),
			new TextboxElement({
				key: 'emailAddress',
				label: 'Email',
				value: 'email',
				order: 2
			})
		];
		console.log('_______ Elements: ' + JSON.stringify(elements));
		return elements.sort((a, b) => a.order - b.order);
	}
}



