import { Component, OnInit, ViewChild, EventEmitter } from '@angular/core';
import { Router }                                     from '@angular/router';
import { Subscription }                               from 'rxjs/Subscription';

// COMPONENTS
import { CardComponent }    from '../../../components/basic/card/card.component';
import { ConfirmComponent } from '../../../components/basic/dialogs/confirm/confirm.component';
import { AlertComponent }   from '../../../components/basic/dialogs/alert/alert.component';

// PROVIDERS
import { DataSectionsService } from '../../../providers/sections/data-sections.service';
// SERVICES
import { SectionsService }     from '../../../providers/sections/sections.service';


@Component({
  selector: 'app-main',
  templateUrl: './main.component.html',
  styleUrls: ['./main.component.scss']
})
export class MainComponent implements OnInit {
 	@ViewChild(CardComponent) private cardComponent: CardComponent;
  @ViewChild(ConfirmComponent) private dialogConfirm: ConfirmComponent;
  @ViewChild(AlertComponent) private dialogAlert: AlertComponent;

  dataForCard: Array<any>;
  itemDeleteTemp: any;
	flagDialog:string;
  isCompleteSection:boolean = true;
  subscriptionIsCompleteSection:Subscription;
	isInformative:boolean;
  constructor(
    private router: Router,
    private sectionsService:SectionsService,
    private dataSectionsService:DataSectionsService) {}

  ngOnInit() {
    this.getSectionsService();
		this.getCatalogOptionsMetadataService();
		this.getCatalogCategoriesService();
  }




  editItem(item:any, isInformative:boolean){
		this.isInformative = isInformative;
		if(!item.esNew && this.verificarSeccionEnEdicion()){
			this.dialogAlert.openModal('Tienes una seccin en edicin');
		}else{
	    this.dataSectionsService.setTemporarySection(item);
	    this.goToAddSection();
		}
  }

	addNewItem(isInformative:boolean){
		this.isInformative = isInformative;
		if(this.verificarSeccionEnEdicion()){
			this.dialogAlert.openModal('Tienes una seccin en edicin');
		}else{
			this.dataSectionsService.setIsInformative(isInformative);
			this.dataSectionsService.setTemporarySection();
			this.goToAddSection();
		}
	}

	outAlertDialog(eve:any){
		console.log("==>> EN outAlertDialog..." + eve);
	}


  deletedItem(item:any){
    this.itemDeleteTemp = item;
    this.dialogConfirm.openModal('Estas seguro que quieres eliminar por completo esta seccin?');
  }


  goToAddSection(){
		let route = this.isInformative ? 'add-section-informative' : '/add-section-service';
    this.router.navigate([route, { outlets: { sub: ['profile-general'] }}]);
  }

  // *** SERVICIOS
  getSectionsService() {
    // TODO CONSUMIR SERVICIO CATALOGO SECCIONES
    this.sectionsService.getSections().then(sections => {
      this.dataSectionsService.setCatalogSections(sections);

      if (this.verificarSeccionEnEdicion()) {
        this.dataForCard = this.dataSectionsService.addNewSectionToCatalog();
      } else {
        this.dataForCard = this.dataSectionsService.getCatalogSections();
      }

    });
  }

	getCatalogOptionsMetadataService(){
		this.sectionsService.getCatalogOptionsMetadata().then(catalog => {
			this.dataSectionsService.setCatalogOptionsMetadata(catalog);
		});
	}

	getCatalogCategoriesService(){
		this.sectionsService.getCatalogCategories().then(catalog => {
			this.dataSectionsService.setCatalogCategories(catalog);
		});
	}

  verificarSeccionEnEdicion() {

    return this.dataSectionsService.isTherePendingSection();
  }

  viewItem(item: any) {
    // TODO CONSUMIR SERVICIO CAMBIO VISTA MOVIL
    this.sectionsService.changeViewSection(item).then(success => {
      if (success) {
        // this.getSectionsService();//HABILITAR PARA PRODUCCION
        // ELIMINAR PARA PRODUCCION
        this.dataForCard = this.dataSectionsService.changeViewSectionData(item, this.dataForCard);
      }
    });
  }

  outConfirmDialog(eve: any) {
    if (eve) {
      if (this.itemDeleteTemp.esNew) {
        this.dataSectionsService.setIsCompleteSection(true);
        this.dataSectionsService.cleanData();
        this.dataForCard = this.dataSectionsService.deleteSectionData(this.itemDeleteTemp, this.dataForCard);
      } else {
        // TODO CONSUMIR SERVICIO DE CAMBIOS DE ESTATUS (ELIMINAR) DE SECCION
        this.sectionsService.changeStatusSection(this.itemDeleteTemp).then(success => {
          if (success) {
            // this.getSectionsService();//HABILITAR PARA PRODUCCION
            // ELIMINAR PARA PRODUCCION
            this.dataForCard = this.dataSectionsService.deleteSectionData(this.itemDeleteTemp, this.dataForCard);
          }
        });
      }
    }
		}

}



