import { Injectable } from '@angular/core';
import { CanActivate, ActivatedRouteSnapshot, RouterStateSnapshot, Router } from '@angular/router';
import { Observable } from 'rxjs/Observable';
// PROVIDERS
import { DataSectionsService } from '../providers/sections/data-sections.service';

@Injectable()
export class TestCanActiveGuard implements CanActivate {

	constructor(
		private dataSectionsService:DataSectionsService,
		private router: Router){}

  canActivate(
    next: ActivatedRouteSnapshot,
    state: RouterStateSnapshot): Observable<boolean> | Promise<boolean> | boolean {

    console.log('==>>Llamando canActivate ');
    console.log("==>> ActivatedRouteSnapshot " + next);
    console.log("==>> RouterStateSnapshot " + state);

		let activeState:boolean = this.dataSectionsService.getTemporarySection() ? true : false;
		console.log("==>> CAN ACTIVE GUARD: " + activeState);

		if(activeState){
			return true;
		}

		this.router.navigate(['']);
    return false;
  }



  canActivateChild(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): boolean {
    console.log('==>>Llamando canActivateChild ');
    console.log("==>> ActivatedRouteSnapshot " + route);
    console.log("==>> RouterStateSnapshot " + state);

    return true;
  }

}



